import * as React from 'react';

import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';

import { Layout, menuHeight } from '../components/layout';
import { Seo } from '../components/seo';
import { Footer } from '../components/footer';
import { Contact } from '../components/contact';

const gap1: number = 20;

const Sub = (props: any) => {
  const { children, title }: { children: React.ReactNode; title: string } =
    props;

  return (
    <Box
      component='span'
      sx={{
        '&::before': {
          content: `"${title}"`,
          display: 'block',
          color: 'primary.main',
          mb: 0,
          top: 0,
          left: 0,
          fontSize: '1rem',
          letterSpacing: 2,
          lineHeight: 1
        }
      }}
    >
      {children}
    </Box>
  );
};

const Page = () => {
  const [isLoaded, setIsLoaded] = React.useState<boolean>(false);

  React.useEffect(() => {
    setIsLoaded(true);
  }, []);

  return (
    <Layout
      menu={[
        {
          label: 'トップページ',
          link: '/'
        },
        {
          label: '採用情報',
          link: '/recruit'
        },
        {
          label: 'お問い合わせ',
          link: '#contact'
        }
      ]}
    >
      <Box
        sx={{
          transition: 'opacity 2000ms ease-out',
          opacity: isLoaded ? 1 : 0,
          overflow: 'hidden'
        }}
      >
        <Box bgcolor='background.default' pt={menuHeight + 'px'}>
          <Container maxWidth='lg'>
            <Box
              sx={{
                position: 'relative',
                display: 'flex',
                flexDirection: 'column',
                gap: 20
              }}
            >
              <Box component='section' id='recruit'>
                <Typography variant='h1'>
                  <Sub title='NOT FOUND'>404</Sub>
                </Typography>
                <Box display='flex' flexDirection='column' gap={gap1}>
                  <Box>
                    <Typography variant='h2' color='primary'>
                      お探しのページが見つかりませんでした。
                    </Typography>
                    <Typography>
                      上部メニューから他のページを選択してください。
                      <br />
                      何かご質問やお問い合わせがございましたら、以下からお気軽にご連絡ください。
                    </Typography>
                  </Box>
                </Box>
              </Box>
              <Contact />
            </Box>
          </Container>
        </Box>
        <Footer />
      </Box>
    </Layout>
  );
};

export default Page;

export const Head = () => (
  <Seo title='お探しのページが見つかりませんでした。' />
);
